.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fruits-desktop {
  display: none;
}

@media (min-width: 1024px) {
  .desktop-image {
      display: block;
      width: 100%;
      height: 100vh;
  }
  .fruits-desktop {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 100vh;
      object-fit: cover;
      z-index: 10;
  }
}
